import React from 'react';
import useForm from './form';


const ChangePassForm = (props) => {
    const stateSchema = {
        oldpassword: { value: '', error: '' },
        newpassword: { value: '', error: ''}
    };

    const validationStateSchema = {
        oldpassword: {
          required: true
        },
        newpassword: {
          required: true
        }
      };

      const { state, handleOnChange, handleOnSubmit, disable } = useForm(
        stateSchema,
        validationStateSchema,
        props.method
      );

      return (
        <div>
            <div className="form-group">
                <label for="oldpassword">Old Password</label>
                <input onChange={handleOnChange} name="oldpassword" type="password" className="form-control" id="oldpassword" placeholder="Enter old password"/>
                {state.oldpassword.error && <p className="invalid-feedback">{state.oldpassword.error}</p>}
            </div>
            <div className="form-group">
                <label for="newpassword">New Password</label>
                <input onChange={handleOnChange} name="newpassword" type="password" className="form-control" id="newpassword" placeholder="Enter new password"/>
                {state.newpassword.error && <p className="invalid-feedback">{state.newpassword.error}</p>}
            </div>
            <button disabled={props.loading || disable?true:false} onClick={handleOnSubmit} type="submit" className="btn btn-primary">Change Password {props.loading?(<i className="fa fa-circle-o-notch fa-spin ml-3"></i>):null}</button>
        </div>
      );
}

export default ChangePassForm;